import RestaurantsSource from "../../data/restaurants-source";
import {
  createTopRatedRestaurant,
  createErrorConection
} from "../templates/template-card-restaurant";
const TopRatedRestaurants = {
  async render() {
    return `
        <div class="list-top-rated">
        </div>
      `;
  },

  async afterRender() {
    try{
      const restaurants = await RestaurantsSource.getRestaurants();
      const restaurantContainer = document.querySelector(".list-top-rated");
      const topRatedRestaurant = restaurants.filter(
        (restaurant) => restaurant.rating >= 4.5
      );
      console.log(topRatedRestaurant);
  
      topRatedRestaurant.forEach((restaurant) => {
        restaurantContainer.innerHTML += createTopRatedRestaurant(restaurant);
      });
    }catch (error){
      let restaurantContainer = document.querySelector(".list-top-rated");
      restaurantContainer.innerHTML = '';
      restaurantContainer.innerHTML = createErrorConection();
    }
  }
};

export default TopRatedRestaurants;
