import FavoriteRestaurantsIdb from "../../data/favorite-restaurants-idb";
import {
  createRestaurantCardTemplate,
  createErrorConection
} from "../templates/template-card-restaurant";

const Like = {
  async render() {
    return `
      <div id="main-content-favorite">
        <h1 tabindex="-1">Favorite Restaurant</h1>
        <div class="card-container"></div>
      </div>
      `;
  },

  async afterRender() {
    try {
      const restaurants = await FavoriteRestaurantsIdb.getAllRestaurants();
      const restaurantsContainer = document.querySelector(".card-container");

      restaurants.forEach((restaurant) => {
        restaurantsContainer.innerHTML +=
          createRestaurantCardTemplate(restaurant);
      });
    } catch (error) {
      let favoriteContainer = document.querySelector("#main-content-favorite");
      favoriteContainer = "";
      favoriteContainer = createErrorConection();
    }
  }
};

export default Like;
