import CONFIG from "../../globals/config";

const createRestaurantCardTemplate = (restaurant) => `
    <div class="card" tabindex="-1">
        <p tabindex="0">${restaurant.city}</p>
        <img tabindex="0" src="${
          CONFIG.BASE_IMAGE_URL + restaurant.pictureId
        }" alt="gambar restaurant ${restaurant.name}">
        <p tabindex="0">Rating: ${restaurant.rating}</p>
        <p tabindex="0">
        <a href="/#/detail/${restaurant.id}">
        ${restaurant.name}
        </a></p>
    </div>
`;

const createRestaurantDetailContent = (restaurant) => `
    <h2>${restaurant.name}</h2>\
    <div id="category"></div>
    <div class="detailContent">
        <div class="imgDetail">
            <img src="${CONFIG.BASE_IMAGE_URL + restaurant.pictureId}" 
            alt="${restaurant.name}" />
        </div>
        <div class="information">
            <h4>City</h4>
            <p>${restaurant.city}</p>
            <h4>Rating</h4>
            <p>${restaurant.rating}</p>
            <h4>Address</h4>
            <p>${restaurant.address}</p>
            <h4>Description</h4>
            <p>${restaurant.description}</p>
        </div>
    </div>
`;
const createTopRatedRestaurant = (restaurant) => `
    <div class="container-top-rated">
        <div class="img">
        <p tabindex="0">${restaurant.city}</p>
        <img src="${CONFIG.BASE_IMAGE_URL + restaurant.pictureId}" 
        alt="${restaurant.name}" tabindex="0">
        </div>
        <div class="content-top-rated">
        <p tabindex="0">Rating: ${restaurant.rating}</p>
        <p tabindex="0">
        <a href="/#/detail/${restaurant.id}">
        ${restaurant.name}
        </a></p>
        <p tabindex="0">${restaurant.description}</p>
        </div>
    </div>
`;
const createCategoriesContent = (categories) => {
  let categoryContent = "";
  categories.forEach((category) => {
    categoryContent += `<p>${category.name}</p>`;
  });
  return categoryContent;
};

const createLikeButtonTemplate = () => `
  <button aria-label="like this restaurant" id="likeButton" class="like">
  </button>
`;

const createLikedButtonTemplate = () => `
  <button aria-label="unlike this restaurant" id="likeButton" class="liked">
  </button>
`;

const createErrorSearch = (value) => `
  <p class="error-message"> Hasil pencarian ${value} tidak ada!</p>
`;

const createDrinksContent = (drinks) => {
  let drinksContent = "";
  drinks.forEach((drink) => {
    drinksContent += `<p>${drink.name}</p>`;
  });
  return drinksContent;
};

const createFoodsContent = (foods) => {
  let foodsContent = "";
  foods.forEach((food) => {
    foodsContent += `<p>${food.name}</p>`;
  });
  return foodsContent;
};

const createReviewContent = (customerReviews) => {
  let reviewContent = "";
  customerReviews.forEach((review) => {
    reviewContent += `
    <div class="reviewContent">
        <p>Date: ${review.date}</p>
        <p>Name: ${review.name}</p>
        <p>Review: ${review.review}</p>
    </div>
    `;
  });
  return reviewContent;
};

const createErrorConection = () => `
  <p class="error-message"> Ooopss! it looks like you're offline</p>
`;

export {
  createRestaurantCardTemplate,
  createRestaurantDetailContent,
  createCategoriesContent,
  createTopRatedRestaurant,
  createLikeButtonTemplate,
  createLikedButtonTemplate,
  createErrorSearch,
  createDrinksContent,
  createFoodsContent,
  createReviewContent,
  createErrorConection
};
