import { async } from "regenerator-runtime";
import RestaurantsSource from "../../data/restaurants-source";
import {
  createRestaurantCardTemplate,
  createErrorSearch,
  createErrorConection
} from "../templates/template-card-restaurant";
const Restaurants = {
  async render() {
    return `
      <div id="main-content-restaurants">
        <h1 tabindex="-1">Explore Restaurant</h1>
        <section class="search-container">
        <input type="text" id="searchInput" class="searchInput" name="Search Restaurant by Name and City"
            placeholder="Search Restaurant by Name and City" for="searchBtn">
        <button id="searchBtn" type="submit" aria-label="button search"></button>
        </section>
        <div class="card-container"></div>
      </div>
      `;
  },

  async afterRender() {
    try {
      const restaurants = await RestaurantsSource.getRestaurants();
      console.log(restaurants);
      const restaurantContainer = document.querySelector(".card-container");

      restaurants.forEach(
        (restaurant) =>
          (restaurantContainer.innerHTML +=
            createRestaurantCardTemplate(restaurant))
      );

      document
        .getElementById("searchBtn")
        .addEventListener("click", async function () {
          restaurantContainer.innerHTML = "";
          const searchValue = document.querySelector("#searchInput").value;
          const searchRestaurant = await RestaurantsSource.searchRestaurant(
            searchValue
          );
          if (searchRestaurant.length === 0) {
            restaurantContainer.innerHTML += createErrorSearch(searchValue);
          }
          searchRestaurant.forEach((restaurant) => {
            restaurantContainer.innerHTML +=
              createRestaurantCardTemplate(restaurant);
          });
        });
    } catch (error) {
      let mainContainerRestaurants = document.querySelector(
        "#main-content-restaurants"
      );
      mainContainerRestaurants.innerHTML = "";
      mainContainerRestaurants.innerHTML = createErrorConection();
    }
  }
};

export default Restaurants;
