import UrlParser from "../../routes/url-parser";
import RestaurantsSource from "../../data/restaurants-source";
import {
  createRestaurantDetailContent,
  createCategoriesContent,
  createDrinksContent,
  createFoodsContent,
  createReviewContent,
  createErrorConection
} from "../templates/template-card-restaurant";
import LikeButtonInitiator from "../../utils/like-button-initiator";

const Detail = {
  async render() {
    return `
    <div id="main-content-detail">
      <div id="errorContainer"></div>
      <div id="categoryDetail" class="restaurant_detail"></div>
      <div id="restaurantDetail" class="restaurant_detail"></div>
      <div id="MenuAndlikeButtonContainer">
        <div id="foodsContainer">
          <h2>Foods</h2>
        </div>
        <div id="drinksContainer">
          <h2>Drinks</h2>
        </div>
        <div id="btnLikeContainer">
        </div>
      </div>
      <div class="wrap-review-content">
        <div class="review-content">
          <div class="head-review-content">
            <h2>Review</h2>
            <div class="add-review-content">
              <a href=""> </a>
            </div>
          </div>
          <div id="reviewContainer">
          </div>
        </div>
      </div>
    </div>
    `;
  },

  async afterRender() {
    try {
      const url = UrlParser.parseActiveUrlWithoutCombiner();
      const restaurant = await RestaurantsSource.detailRestaurant(url.id);
      console.log(restaurant);
      // Get Container
      const restaurantContainer = document.querySelector("#restaurantDetail");
      const restaurantCategoryContainer =
        document.querySelector("#categoryDetail");
      const drinksContainer = document.querySelector("#drinksContainer");
      const foodsContainer = document.querySelector("#foodsContainer");
      const reviewContainer = document.querySelector("#reviewContainer");

      //Input data to Container
      restaurantContainer.innerHTML = createRestaurantDetailContent(restaurant);
      restaurantCategoryContainer.innerHTML = createCategoriesContent(
        restaurant.categories
      );
      drinksContainer.innerHTML += createDrinksContent(restaurant.menus.drinks);
      foodsContainer.innerHTML += createFoodsContent(restaurant.menus.foods);

      LikeButtonInitiator.init({
        likeButtonContainer: document.querySelector("#btnLikeContainer"),
        restaurant: {
          id: restaurant.id,
          name: restaurant.name,
          rating: restaurant.rating,
          pictureId: restaurant.pictureId,
          city: restaurant.city
        }
      });
      reviewContainer.innerHTML = createReviewContent(
        restaurant.customerReviews
      );
    } catch (error) {
      let mainContentDetail = document.querySelector('#main-content-detail');
      mainContentDetail.innerHTML = '';
      mainContentDetail.innerHTML = createErrorConection();
    }
  }
};

export default Detail;
